import { Card } from "@/components/ui/card"
import { Activity, Clock, Database, AlertTriangle } from "lucide-react"

export function DashboardHeader() {
  // Mock data - in production this would come from real-time API
  const kpis = [
    {
      label: "Eficiencia Actual",
      value: "87.3%",
      status: "warning" as const,
      icon: Activity,
      description: "Inyección / Consumo",
    },
    {
      label: "Tiempo de Decisión",
      value: "12 min",
      status: "success" as const,
      icon: Clock,
      description: "Promedio últimas 24h",
    },
    {
      label: "Uso de Datos",
      value: "76%",
      status: "success" as const,
      icon: Database,
      description: "Decisiones basadas en datos",
    },
    {
      label: "Sectores en Riesgo",
      value: "8",
      status: "destructive" as const,
      icon: AlertTriangle,
      description: "Requieren atención",
    },
  ]

  const getStatusColor = (status: "success" | "warning" | "destructive") => {
    switch (status) {
      case "success":
        return "text-success"
      case "warning":
        return "text-warning"
      case "destructive":
        return "text-destructive"
    }
  }

  return (
    <header className="sticky top-0 z-50 border-b bg-card shadow-sm">
      <div className="container mx-auto px-4 py-4">
        <div className="mb-4 flex items-center justify-between">
          <div>
            <h1 className="font-heading text-2xl font-semibold tracking-tight text-foreground">
              Sistema de Agua Potable - León
            </h1>
            <p className="text-sm text-muted-foreground mt-0.5">Dashboard Operativo</p>
          </div>
          <div className="text-right">
            <p className="text-xs text-muted-foreground uppercase tracking-wide">Última actualización</p>
            <p className="text-sm font-medium tabular-nums mt-0.5">{new Date().toLocaleTimeString("es-MX")}</p>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {kpis.map((kpi) => {
            const Icon = kpi.icon
            return (
              <Card key={kpi.label} className="p-4">
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <p className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-2">
                      {kpi.label}
                    </p>
                    <p className={`text-3xl font-bold tabular-nums leading-none ${getStatusColor(kpi.status)}`}>
                      {kpi.value}
                    </p>
                    <p className="text-xs text-muted-foreground mt-2 leading-relaxed">{kpi.description}</p>
                  </div>
                  <Icon className={`h-8 w-8 ${getStatusColor(kpi.status)}`} />
                </div>
              </Card>
            )
          })}
        </div>
      </div>
    </header>
  )
}
